 * @Email: qinrifeng@163.com
 * @Github: https://github.com/FoneQinrf
 -->
<h3>Image 图片展示 <Badge text="v0.05"></Badge></h3>
<hr>
<h4>使用方式</h4>
<pre v-pre><code v-pre class="language-js">
<span class="hljs-keyword">import</span> Vue <span class="hljs-keyword">from</span> <span class="hljs-string">'vue'</span>
<span class="hljs-keyword">import</span> { Image } <span class="hljs-keyword">from</span> <span class="hljs-string">'vvmui'</span>

Vue.component(Image.name, Image)

</code></pre>
<h4>基本用法</h4>
<h5>代码示例</h5>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"Lazyload-wrp"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">v-Image</span>
        <span class="hljs-attr">v-for</span>=<span class="hljs-string">"item in fit"</span>
        <span class="hljs-attr">:key</span>=<span class="hljs-string">"item"</span>
        <span class="hljs-attr">style</span>=<span class="hljs-string">"width: 48%;height: 80px;margin-top: 8px;"</span>
        <span class="hljs-attr">src</span>=<span class="hljs-string">"https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=2075903360,405209795&amp;fm=26&amp;gp=0.jpg"</span>
        <span class="hljs-attr">:fit</span>=<span class="hljs-string">"item"</span>
      /&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">fit</span>: [<span class="hljs-string">"fill"</span>, <span class="hljs-string">"contain"</span>, <span class="hljs-string">"cover"</span>, <span class="hljs-string">"none"</span>, <span class="hljs-string">"scale-down"</span>]
    };
  }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">style</span> <span class="hljs-attr">lang</span>=<span class="hljs-string">"less"</span> <span class="hljs-attr">scoped</span>&gt;</span><span class="css">
<span class="hljs-selector-class">.Lazyload-wrp</span> {
  <span class="hljs-attribute">display</span>: flex;
  <span class="hljs-attribute">flex-wrap</span>: wrap;
  <span class="hljs-attribute">justify-content</span>: space-between;
  <span class="hljs-attribute">padding</span>: <span class="hljs-number">0</span> <span class="hljs-number">15px</span>;
}
<span class="hljs-selector-class">.vvm-Image</span> {
  <span class="hljs-attribute">background</span>: <span class="hljs-number">#fff</span>;
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">style</span>&gt;</span>

</code></pre>
<h4>懒加载</h4>
<h5>代码示例</h5>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"Lazyload-wrp"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">v-Image</span>
        <span class="hljs-attr">v-for</span>=<span class="hljs-string">"item in list"</span>
        <span class="hljs-attr">:key</span>=<span class="hljs-string">"item"</span>
        <span class="hljs-attr">style</span>=<span class="hljs-string">"width: 48%;height: 80px;margin-top: 8px;"</span>
        <span class="hljs-attr">:src</span>=<span class="hljs-string">"item"</span>
        <span class="hljs-attr">lazy</span>
        <span class="hljs-attr">:delay-time</span>=<span class="hljs-string">"2000"</span>
      &gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">Icon</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"placeholder"</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"vvm-loading"</span> <span class="hljs-attr">icon</span>=<span class="hljs-string">"iconloading1"</span> /&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">v-Image</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">list</span>: [
        <span class="hljs-string">"https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=2075903360,405209795&amp;fm=26&amp;gp=0.jpg"</span>,
        <span class="hljs-string">"https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=1141016847,1401366425&amp;fm=26&amp;gp=0.jpg"</span>,
        <span class="hljs-string">"https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=1290722415,3385672457&amp;fm=26&amp;gp=0.jpg"</span>,
        <span class="hljs-string">"https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=1548119743,252552301&amp;fm=26&amp;gp=0.jpg"</span>
      ]
    };
  }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">style</span> <span class="hljs-attr">lang</span>=<span class="hljs-string">"less"</span> <span class="hljs-attr">scoped</span>&gt;</span><span class="css">
<span class="hljs-selector-class">.Lazyload-wrp</span> {
  <span class="hljs-attribute">display</span>: flex;
  <span class="hljs-attribute">flex-wrap</span>: wrap;
  <span class="hljs-attribute">justify-content</span>: space-between;
  <span class="hljs-attribute">padding</span>: <span class="hljs-number">0</span> <span class="hljs-number">15px</span>;
}
<span class="hljs-selector-class">.vvm-Image</span> {
  <span class="hljs-attribute">background</span>: <span class="hljs-number">#fff</span>;
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">style</span>&gt;</span>

</code></pre>
<h4>图片加载失败</h4>
<h5>代码示例</h5>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">v-Image</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"width: 48%;height: 80px"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">span</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"error"</span>&gt;</span>图片加载失败<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">v-Image</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">style</span> <span class="hljs-attr">lang</span>=<span class="hljs-string">"less"</span> <span class="hljs-attr">scoped</span>&gt;</span><span class="css">
<span class="hljs-selector-class">.vvm-Image</span> {
  <span class="hljs-attribute">background</span>: <span class="hljs-number">#fff</span>;
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">style</span>&gt;</span>

</code></pre>
<h3>API</h3>
<h5>Image props</h5>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>src</td>
<td>图片地址，同img的src</td>
<td>String</td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>fit</td>
<td>确定图片如何适应容器框，同原生<a href="https://developer.mozilla.org/en-US/docs/Web/CSS/object-fit" target="_blank">object-fit</a></td>
<td>String</td>
<td><code v-pre>fill</code> <code v-pre>contain</code> <code v-pre>cover</code> <code v-pre>none</code> <code v-pre>scale-down</code></td>
<td>无</td>
</tr>
<tr>
<td>alt</td>
<td>同img标签alt</td>
<td>String</td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>lazy</td>
<td>是否开启懒加载</td>
<td>boolean</td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>delay-time</td>
<td>图片加载完毕时延迟多长时间展示图片</td>
<td>Number</td>
<td>无</td>
<td>无</td>
</tr>
</tbody>
</table>
<h5>Image solt</h5>
<table>
<thead>
<tr>
<th>名称</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>placeholder</td>
<td>图片未加载的占位内容</td>
</tr>
<tr>
<td>error</td>
<td>加载失败的内容</td>
</tr>
</tbody>
</table>
<h5>Image Events</h5>
<table>
<thead>
<tr>
<th>事件名</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>on-load</td>
<td>图片加载成功触发</td>
<td>(Event)=&gt;{}</td>
</tr>
<tr>
<td>on-error</td>
<td>图片加载失败触发</td>
<td>(Event)=&gt;{}</td>
</tr>
</tbody>
</table>
<h5>更新日志</h5>
<table>
<thead>
<tr>
<th style="text-align:center">版本</th>
<th style="text-align:left">类型</th>
<th style="text-align:left">内容</th>
</tr>
</thead>
<tbody></tbody>
</table>
</section></template>
<style lang="less" scoped>
.Lazyload-wrp {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 0 15px;
}
.vvm-Image {
  background: #fff;
}
</style><style lang="less" scoped>
.Lazyload-wrp {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 0 15px;
}
.vvm-Image {
  background: #fff;
}
</style><style lang="less" scoped>
.vvm-Image {
  background: #fff;
}
</style>
<script>
export default {
  data() {
    return {
      fit: ["fill", "contain", "cover", "none", "scale-down"]
    };
  }
};
</script>


